import axios from 'axios';

export const sendMessageToTelegram=async (message)=>{
    const TELEGRAM_CHANNEL_CHAT_ID=-1002124775547
    const TELEGRAM_URI_API='https://api.telegram.org/bot1626147624:AAHV7312aD8OkajGjhneDzevfPxDZLgyAh0/sendMessage';
    // console.log("TELEGRAM_URI_API=", process.env.TELEGRAM_URI_API);
    // console.log("TELEGRAM_CHANNEL_CHAT_ID=", process.env.TELEGRAM_CHANNEL_CHAT_ID);
        console.log("message=", message);
        const response = await axios.post(TELEGRAM_URI_API, {
            chat_id:TELEGRAM_CHANNEL_CHAT_ID,
            parse_mode: 'html',
            text: message,
             
        });
        return response;
        
    
    
   
}